.signupScreen{
    max-width: 250px;
    padding: 45px;
    margin-left: auto;
    margin-right: auto;
    /* padding-top: 10px; */
    /* margin-bottom: 40%; */
    background: rgba(
         0, 0, 0, 0.75); 
}

.signupScreen > form > h1{
    text-align: left;
    margin-bottom: 25px;
}

.signupScreen > form > h4{
    text-align: left;
    margin-top: 15px;
}

.signupScreen__grey{
    color: grey;
}

.signupScreen_link:hover{
    cursor: pointer;
    text-decoration: underline;
}

.signupScreen > form{
    display: flex;
    flex-direction: column;
}

.signupScreen > form > input{
    outline-width: 0;
    height: 40px;
    margin-bottom: 14px;
    border-radius: 5px;
    border: none;
    padding: 5px 15px; 
}

.signupScreen > form > button{
 
    padding: 16px 20px;
    font-size: 1rem;
    color: #fff;
    border-radius: 5px;
    background-color: #e50914;
    font-weight: 600;
    border: none;
    cursor: pointer;
    margin-top: 20px; 
}





