.loginScreen{
    position: relative;
    height: 100%;
    background: url("https://assets.nflxext.com/ffe/siteui/vlv3/757ab38f-5d08-40bc-b3eb-eaba63ed8203/93c34f94-56c8-40a7-8b2e-b4aac6427977/GB-en-20210125-popsignuptwoweeks-perspective_alpha_website_medium.jpg") center no-repeat;
    background-size: cover;
}

.loginScreen__body{
    position: absolute;
    top: 30%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    z-index: 1;
    color: #fff;
    padding: 20px;
    /* margin-left: 24%; */
    left: 0;
    right: 0;
}   

.loginScreen__body>h1{
    font-size: 2.125rem;
    margin-bottom: 20px;
}

.loginScreen__body>h2{
    font-size: 2rem;
    font-weight: 400;
    margin-bottom: 30px;
}

.loginScreen__body>h3{
     font-size: 1.3rem;
     font-weight: 400;
}

.loginScreen__gradient{
     width: 100%;
     z-index: 1;
     height: 100vh;
     background: rgba(0, 0, 0, 0.4);
     background-image: linear-gradient(
         to top,
         rgba(0,0,0.8)0,
         rgba(0,0,0,0)60%,
         rgba(0,0,0.8)100%
     ); 
}




.loginScreen_button{
    position: fixed;
    right: 20px;
    top: 20px;
    padding: 10px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #e50914;
    font-weight: 600;
    border: none;
    cursor: pointer;
}


.loginScreen__logo{
    position: fixed;
    left: 0;
    width: 150px;
    object-fit: contain;
    padding-left: 20px;
}

.loginScreen__input>form>input{
    padding:10px;
    outline-width: 0;
     height: 30px;
     width: 30%;
     border: none;
     max-width: 600px;
}

.loginScreen__getSrarted{
    padding: 12px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #e50914;
    border: none;
    font-weight: 600;
    cursor: pointer;
}

.loginScreen__input{
    margin: 20px;
}